// NotFound.tsx

import React, { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { Home, Flame } from "lucide-react";
import gsap from "gsap";

const NotFound: React.FC = () => {
  const navigate = useNavigate();
  const containerRef = useRef<HTMLDivElement>(null);
  const titleRef = useRef<HTMLHeadingElement>(null);
  const messageRef = useRef<HTMLParagraphElement>(null);
  const buttonRef = useRef<HTMLButtonElement>(null);
  const glowRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const tl = gsap.timeline({ defaults: { ease: "power2.out" } });

    tl.from(glowRef.current, {
      duration: 1.2,
      scale: 0,
      opacity: 0,
    })
      .from(
        titleRef.current,
        {
          duration: 0.8,
          y: 40,
          opacity: 0,
          scale: 0.8,
        },
        "-=0.8"
      )
      .from(
        messageRef.current,
        {
          duration: 0.6,
          y: 30,
          opacity: 0,
        },
        "-=0.4"
      )
      .from(
        buttonRef.current,
        {
          duration: 0.6,
          y: 30,
          opacity: 0,
        },
        "-=0.3"
      );

    // Continuous glow animation
    gsap.to(glowRef.current, {
      duration: 2,
      scale: 1.1,
      opacity: 0.6,
      repeat: -1,
      yoyo: true,
      ease: "sine.inOut",
    });
  }, []);

  const handleGoHome = () => {
    navigate("/");
  };

  return (
    <div
      ref={containerRef}
      className="min-h-screen flex items-center justify-center bg-[#0d0a14] px-4 relative overflow-hidden"
    >
      {/* Background gradient effects */}
      <div className="absolute inset-0 bg-gradient-to-br from-orange-900/20 via-transparent to-red-900/20" />
      <div className="absolute top-1/4 left-1/4 w-96 h-96 bg-orange-500/10 rounded-full blur-3xl" />
      <div className="absolute bottom-1/4 right-1/4 w-96 h-96 bg-red-500/10 rounded-full blur-3xl" />

      <div className="relative max-w-lg w-full text-center space-y-8 z-10">
        {/* Animated Glow Background */}
        <div
          ref={glowRef}
          className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2 w-64 h-64 bg-gradient-to-r from-orange-500/30 via-yellow-500/20 to-red-500/30 rounded-full blur-3xl"
        />

        {/* Fire Icon */}
        <div className="relative flex justify-center mb-4">
          <div className="w-24 h-24 bg-gradient-to-br from-orange-500 via-orange-600 to-red-600 rounded-2xl flex items-center justify-center shadow-2xl shadow-orange-500/40 animate-pulse">
            <Flame className="h-12 w-12 text-white" />
          </div>
        </div>

        {/* Error Code with Neon Effect */}
        <h1
          ref={titleRef}
          className="text-8xl md:text-9xl font-black bg-gradient-to-r from-yellow-400 via-orange-500 to-red-500 bg-clip-text text-transparent drop-shadow-[0_0_30px_rgba(251,146,60,0.5)]"
          style={{
            textShadow: '0 0 40px rgba(251, 146, 60, 0.4), 0 0 80px rgba(251, 146, 60, 0.2)',
          }}
        >
          404
        </h1>

        {/* Error Message */}
        <div ref={messageRef} className="space-y-3">
          <p className="text-2xl font-semibold text-white">
            Page Not Found
          </p>
          <p className="text-gray-400 text-lg">
            Oops! The page you're looking for seems to have vanished into the void.
          </p>
        </div>

        {/* Call-to-Action Button */}
        <button
          ref={buttonRef}
          onClick={handleGoHome}
          className="group relative inline-flex items-center justify-center px-8 py-4 text-lg font-semibold text-white bg-gradient-to-r from-orange-500 via-orange-600 to-red-600 rounded-xl shadow-lg shadow-orange-500/30 hover:shadow-orange-500/50 transition-all duration-300 hover:scale-105 overflow-hidden"
        >
          {/* Button glow effect */}
          <span className="absolute inset-0 bg-gradient-to-r from-yellow-400 via-orange-500 to-red-500 opacity-0 group-hover:opacity-100 transition-opacity duration-300" />
          <span className="relative flex items-center gap-2">
            <Home className="h-5 w-5" />
            Go to Homepage
          </span>
        </button>

        {/* Decorative bottom text */}
        <p className="text-gray-600 text-sm mt-8">
          Error Code: <span className="text-orange-400 font-mono">404_PAGE_NOT_FOUND</span>
        </p>
      </div>
    </div>
  );
};

export default NotFound;
